<template>
	<div id="app">
		<div class="signInForm" v-if="notSigned">
			<button @click="join" v-if="notIn">Sign in with Google</button>
			<button @click="enter" v-if="!notIn">Login</button>
		</div>

		{{ username }}
		<img :src="imgUrl">

		<form @submit.prevent="sendMesssage">
			<input type="text" v-model="messageBox" placeholder="Type a message...">
			<button id="send" v-if="messageBox !== ''">Send</button>
		</form>
	</div>
</template>

<script>

import firebase from 'firebase'
import db from '@/db'
import '@/styles/style.css'

export default {
	name: "app",

	data () {
		return {
			imgUrl: '',
			username: 'Username',
			messageBox: '',
			notSigned: true,
			notIn: true
		}
	},

	methods: {
		enter () {
			var user = firebase.auth().currentUser;

		  	this.username = user.displayName;
		  	this.imgUrl = user.photoUrl;
		  	this.notIn = false
		  	this.notSigned = false
		},

		join () {
			var user = firebase.auth().currentUser;

		  	if (!user) {
			  	this.notSigned = true;
			  	this.notIn = true;
	      		var provider = new firebase.auth.GoogleAuthProvider();

	      		firebase.auth()
	      		.signInWithPopup(provider)

	      		.then(function(result) {
	               var token = result.credential.accessToken;
	               var user = result.user;
	     		})             

	     		.catch(function(error) {
	               var errorCode = error.code;
	               var errorMessage = error.message;
	               var email = error.email;
	               var credential = error.credential;
	        	})
		  	}
	  		this.notIn = false;
		}
	}
};

</script>